import React, {useRef} from 'react'
import Spinner from 'components/Spinner'
import ListOfGifs from 'components/ListOfGifs'
import {useGifs} from 'hooks/useGifs'
import './searchResults.css'
import useNearScreen from 'hooks/useNearScreen'
// import debounce from 'just-debounce-it'
import { useEffect } from 'react'
import { useCallback } from 'react'
import throttle from 'just-throttle'

export default function SearchResults ({ params }) {
  const { keyword } = params
  const { loading, gifs, setPage } = useGifs({ keyword })
  const externalRef = useRef()
  const {isNearScreen} = useNearScreen({ externalRef: loading ? null : externalRef, once: false })
  // const debounceHandleNextPage = useRef()
  // console.log(isNearScreen)
   // const handleNextPage = ()=> setPage(prevPage => prevPage + 1)
  // const handleNextPage = () => console.log('next page')

  const debounceHandleNextPage = useCallback(throttle(
    ()=> setPage(prevPage => prevPage + 1), 300
  ), [setPage])

  useEffect(function () {
    if (isNearScreen) debounceHandleNextPage()
  }, [debounceHandleNextPage, isNearScreen])

  return <>
    {loading
      ? <Spinner />
      : <>
        <h3 className="App-title">{decodeURI(keyword).charAt(0).toUpperCase()+decodeURI(keyword).slice(1)}</h3>
        <ListOfGifs gifs={gifs} />
        <div id="visor" ref={externalRef}>Powered by <a href='https://giphy.com'>giphy.com</a></div>
      </>
    }
    {/* <br />
    <br />
    <button className='next__page--btn' onClick={handleNextPage}>Get next page</button> */}
  </>
}